	methods: {
		login() {
			uni.login({
				provider: 'weixin',
				success: (res) => {
					console.log(res.code);
					let code = res.code;
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							console.log(infoRes);
							console.log(infoRes.encryptedData)
							console.log(infoRes.iv);
							uni.request({
								url: 'http://a.chddapp.com/chddv8/asale/account/weappLogin',
								method: 'POST',
								header: {
									"Content-Type": 'application/x-www-form-urlencoded'
								},
								data: {
									codeLogin: res.code,
									encryptedData: infoRes.encryptedData,
									iv: infoRes.iv
								},
								success:(res)=> {
									console.log(res)
								},
								fail: (res) => {
									console.log(res)
								}
							})
						}
					});
				},
				fail: (res) => {
					console.log(res)
				}
			})
		}
	}
